@import '../../SCSS/variables.scss';

#video {
    padding: 56px 64px 112px 64px;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
}

#video::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgba(21, 35, 56, 0.5) 0%, rgba(24, 40, 64, 0.95) 100%);
}

#video .container {
    position: relative;
}

.video-title-wrapper {
    display: flex;
    justify-content: space-between;
    color: #fff;
    margin-bottom: 25px;
    h2 {
        font-size: $fz_32;
    }
}

.video-title-wrapper i {
    color: #fff;
}

.video-title-wrapper .more-news {
    display: flex;
    align-items: center;
}

.more-videos {
    display: none;    
}

.video-content-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px;
}

.video-main-item-container {
    text-align: left;
    color: #fff;    
    span {    
        font-size: $fz_12;
        margin-top: 32px;
        display: block;
    }
    p {
        font-size: $fz_32;
        font-weight: 600;
        margin-top: 24px;
        text-decoration-line: underline;
        text-decoration-style: solid;
        text-decoration-color: rgba(255, 255, 255, 0);
        transition: all 0.2s ease-in-out;
        &:hover {
            text-decoration-color: rgba(255, 255, 255, 1);
            cursor: pointer;
        }
    }
}

.video-main-item-container .play-icon{
    position: absolute;
    left: 16px;
    bottom: 22px;
    max-width: 40px;
    max-height: 40px;
}

.video-main-img {
    display: grid;
    grid-template-columns: 1fr;
}

.video-main-item-box {
    position: relative;
    transition: all 0.2s ease-in-out;
    &:hover {
        transform: scale(1.01);
        cursor: pointer;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    }
}

.video-main-item-img {
    width: 100%;
}

.video-items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px;    
}

.video-item {
    color: #fff;
    text-align: left;
    span {
        display: block;
        font-size: $fz_12;
        margin-top: 16px;
    }
    p {
        font-size: $fz_16;
        font-weight: 600;
        margin-top: 8px;
        position: relative;
        cursor: pointer;
        // text-decoration: underline;
        // text-decoration-color: transparent;
        // transition: all 0.2s ease-in-out;
        &::after {
            content: '';
            position: absolute;
            bottom: -10px;
            left: -4px;
            width: 0%;
            height: 1px;
            background-color: #fff;
            transition: all 0.3s ease-in-out;
        }
        &:hover::after{
            // text-decoration-color: #fff;
            width: 100%;            
        }
    }
}

.video-item-img-container {
    position: relative;
    transition: all 0.2s ease-in-out;
    &:hover {
        transform: scale(1.02);
        cursor: pointer;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    }
}

.video-item-img-container .item-img {
    // max-width: 304px;
    width: 100%;
    object-fit: cover;
}

.video-item-img-container .play-icon {
    position: absolute;
    left: 8px;
    bottom: 14px;
    max-width: 20px;
    max-height: 20px;
}

.overlay {
    top: 0;
    position: absolute;
    height: 100%;
    width: 100%;
    background: linear-gradient(180deg, rgba(21, 35, 56, 0.5) 0%, rgba(24, 40, 64, 0.95) 100%);
}

@media (max-width: 1400px) {

    #video .container {
        max-width: 80%;
    }
    
    .video-content-wrapper {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 992px) {
    #video {
        padding: 56px 44px;
    }

    #video .container {
        max-width: 90%;
    }
}

@media (max-width: 768px) {
    #video {
        padding: 56px 24px;
    }
}

@media (max-width: 700px) {    
    .video-main-item-container {
        p {
            font-size: $fz_16;
            margin-top: 8px;
        }
    }

    .video-main-item-container span {
        margin-top: 16px;
    }

    .video-main-item-box .play-icon{
        max-width: 30px;
    }

    .video-items {
        grid-template-columns: 1fr;
    }

    .video-item-img-container .play-icon {
        max-width: 30px;
        max-height: 30px;
    }
}

@media (max-width: 540px) {
    #video .container {
        max-width: 100%;
    }

    .video-title-wrapper .more-news {
        display: none;
    }

    .video-title-wrapper h2 {
        font-size: 30px;
    }

    .more-videos {
        display: flex;
        align-items: center;
        font-size: $fz_14;
        a {
            color: $text-color-orange;
        }
    }

    .video-main-item-container .play-icon {
        max-width: 30px;
        left: 8px;
        bottom: 14px;
    }
}

@media (max-width: 420px) {
    .video-main-item-container .play-icon {
        max-width: 20px;
    }

    .video-item-img-container .play-icon {
        max-width: 20px;
        max-height: 20px;
    }
}

