#desktop-slider {
    padding-bottom: 53px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 56px 64px;
}

.slider__controls {
    display: flex;
    justify-content: left;
    align-items: center;
    margin-bottom: 24px;
}

.desktop__arrow_left, .desktop__arrow_right {
    width: 40px;
    height: 40px;
    background: transparent;
    cursor: pointer;
    color: #fff;
    line-height: 40px;
    border: 1px solid #fff;
    border-radius: 50%;
    opacity: 0.7;
}

#desktop-slider .slide-heading {
    font-size: 40px;
    color: #fff;
    padding-top: 238px;
    max-width: 752px;
    text-align: left;
    margin-bottom: 24px;
}

.slider_index {
    margin-left: 24px;
    margin-right: 24px;
    color: #fff;
}

.slider_index .index {
    margin-right: 7px;
}

.slider_index .total {
    opacity: 0.5;
    margin-left: 7px;
}

.slider_items_wraper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 32px;
    max-height: 80px;
    overflow: scroll;
    position: relative;
}

.slider-item {
    width: 304px;
    display: flex;
    align-items: center;
    background-color: rgb(255,255,255, 0.16);
    box-shadow: 0px 6px 16px rgba(0, 0, 0, 0.16);
    border-radius: 12px;
    padding: 8px;
    transition: all 0.5s ease-in-out;
}

.slider-item:hover {
    cursor: pointer;
}

.slider-item-active {
    width: 304px;
    display: flex;
    align-items: center;
    background-color: rgb(255,255,255, 0.32);
    box-shadow: 0px 6px 16px rgba(0, 0, 0, 0.16);
    border-radius: 12px;
    padding: 8px;    
}

.slider-item-img {
    max-width: 64px;
    max-height: 64px;
    margin-right: 16px;
    opacity: 1;
}

.slider-item-img img {    
    border-radius: 4px;
}

.slider-item-descr p{
    color: #fff;
    font-size: 14px;
    text-align: left;
    max-height: 63px;
    line-height: 1.5em;
    overflow: hidden;
    width: 100%;
    user-select: none;
}



@media (max-width: 992px) {
    #desktop-slider {
        padding: 36px 44px;;
    }

    #desktop-slider .slide-heading {
        font-size: 32px;
    }
}

@media (max-width: 768px) {
    #desktop-slider {
        padding: 24px;
    }

    .slider__controls {
        display: none;
    }

    #desktop-slider .slide-heading {
        font-size: 24px;
    }    
}

@media (max-width: 500px) {
    .slider_items_wraper {
        gap: 16px;
    }
}