@import '../../SCSS/variables.scss';

#block-2 {
    background-color: $bg-color-grey;
    padding: 32px 64px 64px 64px;
}

.block-2-wrapper {
    display: grid;
    grid-template-columns: 1fr 3fr;
    gap: 32px;
}

.block-2-columns {
    background-color: #fff;
    padding: 16px 16px 24px;
    border-radius: 12px;
    align-self: start
}

.block-2-columns-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
    .columns {
        color: $text-color-black;
        font-size: $fz_24;
    }
    .all-columns {
        color: $text-color-orange;
        font-size: $fz_14;
    }
}

.block-2-columns-item {
    padding: 24px 0 24px 0;
    text-align: left;
    border-bottom: 1px solid #EBEBEB;
    &:last-child {
        border-bottom: none;
    }
    .block-2-columns-item-header {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        margin-bottom: 16px;
        .block-2-columns-item-header-img {
            margin-right: 16px;
        }
        .item-title .item-name {
            color: $text-color-black;
            font-size: $fz_16;
            margin-bottom: 4px;
        }
        .item-title .item-role {
            font-size: $fz_12;
            color: $text-color-grey;            
            text-transform: uppercase;
        }
    }
    .item-content {
        color: #182840;
        font-size: $fz_16;
        margin-bottom: 12px;
    }
    .item-date {
        color: $text-color-grey;
        font-size: $fz_12;
        text-transform: uppercase;
        align-self: flex-end;
    }
}

.block-2-columns .download-more {
    margin-bottom: 0;
    margin-top: 48px;
}

.politics-container {
    background-color: #fff;
    border-radius: 12px;
    padding: 24px 32px 64px 32px;
}

.politics-title-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    .title-politics {
        color: $text-color-black;
        font-size: $fz_32;        
    }
    .all-news {
        color: $text-color-orange;
        font-size: $fz_14;
    }
}

.politics-container .btn-group {
    align-items: center;
    font-size: 16px;
    display: none;
    margin-bottom: 14px;
}

.politics-container .btn{
    color: #919191;
    padding: 4px 8px;
}

.politics-container .btn-active {
    color: #FF7534;
    margin-right: 8px;
    background: #FFEAE0;
    padding: 4px 8px;
    border-radius: 4px;
}

.block-2-content-container {
    grid-template-columns: 2fr 1fr;
    display: grid;
    gap: 32px;
    .more-news {
        display: none;
        justify-content: left;
        align-items: center;
        margin-top: 32px;
    }
}

.block-2-content-items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 32px; 
    text-align: left;
    .block-2-content-item-img {
        width: 100%;
    }
    .date {
        display: block;
        font-size: $fz_12;
        color: $text-color-grey;
        text-transform: uppercase;
        margin-top: 16px;
        margin-right: 16px;
    }
    .interview {
        display: none;
    }
    p {
        font-size: $fz_16;
        font-weight: 600;
        color: #182840;
        margin-top: 10px;
    }
    .main-item {
        grid-column: 1 /3;
        img {
            width: 100%;
        }
        p {
            font-size: $fz_24;
        }
    }
}

.section-news h3 {
    font-size: $fz_16;
    color: #515151;
    text-align: left;
    text-transform: uppercase;
    margin-bottom: 8px;
}

.section-news-items .more-news {
    display: flex;
    justify-content: left;
    align-items: center;
    margin-top: 32px;
}

.promo {
    background-color: #fff;
    border-radius: 12px;
    padding: 32px 32px 24px 32px;
    margin-top: 24px;
}

.promo-title-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 32px;
    .title-promo {
        color: $text-color-black;
        font-size: $fz_32;        
    }
    .all-news {
        color: $text-color-orange;
        font-size: $fz_14;
    }
}

.promo-main-item {
    display: flex;
    justify-content: flex-start;
    gap: 24px;
    text-align: left;
    margin-bottom: 24px;
    .promo-content {
        .date {
            font-size: $fz_12;
            color: $text-color-grey;
            text-transform: uppercase;
            margin-bottom: 10px;
            display: flex;
            align-items: center;
            .promo-promo {
                font-size: $fz_14;
                color: #979EA9;
                text-transform: capitalize;
            }
        }
        .promo-icon {
            margin-left: 16px;
            margin-right: 10px;
        }
        .promo-descr {
            font-size: $fz_32;
        }
    }
}

.promo-items {
    display: grid;
    position: relative;
}

.promo-items-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 32px;
    overflow-x: auto;
}

.promo-blur-element {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 100%;
    width: 140px;
    display: flex;
    align-items: center;
    z-index: 2;
    &.left-blur {
        display: none;
        left: 0;
        button img {
            transform: rotate(180deg);
        }
    }
    &.right-blur {
        right: 0;
        justify-content: flex-end;
        background: linear-gradient(to right, transparent, #fff 66%);
    }
}

.promo-item {
    background: #F6F6F6;
    border-radius: 8px;
    padding: 16px;
    text-align: left;
    width: 304px;
    transition: all ease 0.2s;
    span {
        display: block;
        margin-bottom: 10px;
    }
    p {
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }    
}

.angle-right-icon {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    border: none;
    background: rgba(255, 255, 255, 0.16);
    box-shadow: 0px 6px 8px rgba(0, 0, 0, 0.24);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.economics-container {
    background: #fff;
    padding: 24px 32px 32px 32px;
    border-radius: 12px;
    margin-top: 24px;
    .btn-group {
        display: none;
    }
}

.economics-container .btn-group {
    align-items: center;
    font-size: 16px;
    display: none;
    margin-bottom: 32px;
}

.economics-container .btn {
    color: #919191;
    margin-right: 8px;
}

.economics-container .btn-active {
    color: #FF7534;    
    background: #FFEAE0;
    padding: 4px 8px;
    border-radius: 4px;
}

.news-online {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    img {
        margin-right: 12px;
    }
    span {
        font-size: $fz_14;
        color: $text-color-orange;
    }
}

.economics-content {
    line-height: 140%;
}

.lifestyle-container {
    background: #fff;
    padding: 24px 32px 32px 32px;
    border-radius: 12px;
    margin-top: 24px;
}

.lifestyle-container .btn-group {
    display: none;
    align-items: center;
    font-size: 16px;
    margin-bottom: 14px;
}

.lifestyle-container .btn {
    color: #919191;
    padding: 4px 8px;
}

.lifestyle-container .btn-active {
    color: #FF7534;
    margin-right: 8px;
    background: #FFEAE0;
    padding: 4px 8px;
    border-radius: 4px;
}

.exclusive {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    img {
        margin-right: 12px;
    }
    span {
        font-size: $fz_14;
        color: $text-color-orange;
    }
}

.lifestyle-content {
    line-height: 140%;
}

@media (max-width: 1400px) {
    .block-2-content-items .main-item {
        grid-column: auto;
        p {
            font-size: $fz_16;
        }
    }

    .block-2-content-items {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 1220px) {
    .promo-main-item {
        flex-direction: column;
    }
}

@media (max-width: 992px) {

    #block-2 {
        padding: 32px 44px 64px 44px;
    }
    
    .block-2-wrapper {
        grid-template-columns: 1fr;
        gap: 24px;
    }

    .block-2-columns {
        background-color: inherit;
        border-radius: 0;
        padding: 8px 0 0 0;
    }

    .block-2-columns-title {
        margin-bottom: 24px;
    }

    .block-2-columns-item {
        display: grid;
        width: 304px;
        height: 250px;
        border-bottom: none;
        background-color: #fff;
        padding: 16px;
        border-radius: 8px;
        .item-content {
            font-size: 20px;
        }
        .item-date {
            margin-top: 12px;
        }
    }

    .block-2-columns .download-more {
        display: none;
    }

    .block-2-columns-items {
        display: grid;        
    }

    .block-2-columns-items-content {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 24px;
        overflow-x: auto;
    }    
}

@media (max-width: 768px) {
    #block-2 {
        padding: 32px 0 0 0;
    }

    .block-2-columns {
        padding: 0 24px 0 24px;
    }    

    .politics-container {
        padding: 56px 24px;
        border-radius: 0;
    }

    .politics-title-wrapper .title-politics {
        font-size: 30px;
    }

    .block-2-content-container {
        grid-template-columns: 1fr;
    }

    .politics-container .block-2-content {
        display: none;
    }

    .section-news h3 {
        display: none;
    }

    .politics-container .btn-group {
        display: flex;
    }

    .promo {
        padding: 40px 24px;
        border-radius: 0;
    }

    .promo-main-item .promo-content .promo-descr {
        font-size: 26px;
    }

    .economics-container {
        padding: 56px 24px;
        border-radius: 0;
    }

    .economics-container .section-news {
        display: none;
    }

    .economics-container .btn-group {
        display: flex;
    }

    .block-2-content-items .main-item p,
    .block-2-content-items p {
        font-size: $fz_22;
    }

    .date-interview {
        display: flex;
        align-items: center;
        margin-top: 16px;
        .date {
            margin-top: 0;
        }
        .interview {
            font-size: 14px;
            color: #979EA9;
            display: flex;
            align-items: center;
            gap: 12px;
            img {
                margin-bottom: 4px;
            }
        }
    }

    .block-2-content-container .more-news {
        display: flex;
    }

    .lifestyle-container {
        padding: 56px 24px;
        border-radius: 0;
    }

    .lifestyle-container .btn-group {
        display: flex;
    }

    .lifestyle-container .block-2-content {
        display: none;
    }

    .lifestyle-container .block-1-news-feed-item {
        // flex-direction: row;
        // gap: 10px;
        .lifestyle-item-descr {
            display: flex;
            justify-content: space-between;
            gap: 10px;
            width: 100%;
        }
    }
}

@media (max-width: 576px) {
    .promo-main-item .promo-content .promo-descr {
        font-size: 22px;
    }
}