@import url(../components/top-menu/top-menu);
@import url(../components/categories/categories);
@import url(../components/statistics/statistics);
@import url(../components/desktop/desktop);


@font-face {
    font-family: 'TT Severs';
    src: url('../font/TTSevers-Medium.ttf');
}

* {
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
    padding: 0;
    margin: 0;
}

nav, footer, header, aside {
    display: block;
}

input, button, textarea {
    font-family: inherit;
}

.app {
    text-align: center;
    margin: 0 auto;
    max-width: 1440px;
    font-family: 'TT Severs';
}

.container {
    max-width: 1312px;
    margin: 0 auto;
}
  
