@import '../../SCSS/variables.scss';

#block-1 {
    padding: 64px 64px 32px 64px;
    background-color: #EBEBEB;
}

.block-1-wrapper {
    display: grid;
    grid-template-columns: minmax(304px, 1fr) 3fr;
    gap: 32px;
}

.block-1-news-feed {
    display: grid;
    grid-template-columns: 1fr;
    padding: 16px;
    background-color: #fff;
    border-radius: 12px;
    height: fit-content;
}

.block-1-news-feed a {
    display: block;
}

.block-1-news-feed-title {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.block-1-news-feed .all-news {
    color: #2D2D2D;
    font-size: 24px;
}
.block-1-news-feed .archive {
    color: #182840;
    font-size: 14px;
    border-bottom: 1px solid #182840;;
}

.block-1-news-feed .btn-group {
    display: flex;
    align-items: center;
    font-size: 16px;
    margin-top: 32px;
    margin-bottom: 8px;
}

.block-1-content-wrapper .btn-group {
    display: none;
    align-items: center;
    font-size: 16px;
    margin-top: 32px;
    margin-bottom: 32px;
}

.block-1-news-feed .btn-active,
.block-1-content-wrapper .btn-active {
    color: #FF7534;
    margin-right: 8px;
    background: #FFEAE0;
    padding: 4px 8px;
    border-radius: 4px;
}

.block-1-news-feed .btn,
.block-1-content-wrapper .btn {
    color: #919191;
    margin-right: 8px;
    padding: 4px 8px;
}

.block-1-news-feed-item {
    padding-top: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid #EBEBEB;
    text-align: left;
    width: 100%;
    .lifestyle-img {
        display: none;
    }
}

.block-1-news-feed-item:last-child {
    border-bottom: none;
    background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0.32) 105.15%);    
}

.block-1-news-feed-item p {
    color: #182840;
}

.block-1-news-feed-item span {
    float: left;
    margin-right: 8px;
    color: #707070;
}

.news-with-photo, .breaking-news {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
}

.news-with-photo img {
    margin-right: 12px;
    margin-bottom: 6px;
}

.news-with-photo span {
    color: #979EA9;
    font-size: 14px;
}

.breaking-news img {
    margin-right: 12px;
}

.breaking-news span {
    color: #FF7534;
}

.block-1-content-wrapper .download-more {
    display: none;
}

button.download-more {
    width: 100%;
    height: 38px;
    background-color: transparent;
    color: #182840;
    border: 1px solid #979EA9;
    border-radius: 4px;
    padding-top: 8px;
    padding-bottom: 7px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.download-more img {
    margin-right: 8px;
}

.download-more span {
    display: block;
    margin-top: 2px;
}

.block-1-article {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 32px;
    row-gap: 40px;
}

.block-1-content-wrapper h2.title-content {
    text-align: left;
    font-size: 32px;
    margin-bottom: 32px;
}

.block-1-content-item {
    text-align: left;
}

.block-1-content-item-img img {
    width: 100%;
}

.block-1-content-item-date {
    color: #707070;
    font-size: 12px;
    margin-top: 18px;
}

.block-1-content-item-content {
    color: #182840;
    font-size: 16px;
    margin-top: 10px;
}

.block-1-regions {
    display: grid;    
    // grid-template-rows: 41rem;
    gap: 16px;
}

.block-1-regions-content {
    display: grid;
    grid-template-columns: repeat(3, minmax(315px, 1fr));
    gap: 16px;    
}

// .block-1-regions .city {
//     width: 314px;
//     scroll-snap-align: start;
// }

.regions-title-wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: 56px;
    margin-bottom: 32px;
}

.regions-title-wrapper h2.title-content {
    margin-bottom: 0;
}

.regions-title-wrapper .all-news {
    color: $text-color-orange;
    font-size: $fz_14;
    align-self: center;
}

.block-1-regions .city {
    background-color: #fff;
    border-radius: 12px;
    padding: 16px 16px 32px 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 24px;
    // position: relative;
}

.city-title {
    color: $text-color-black;
    font-size: $fz_24;
    text-align: left;
    // margin-bottom: 24px;
}

.city-item {
    border-bottom: 1px solid #EBEBEB;
    padding-bottom: 16px;
    margin-top: 16px;
}

.city-item:last-child {
    border-bottom: none;
}

.city-date {
    color: #F3560C;
    font-size: $fz_12;
    text-align: left;
    margin-bottom: 12px;
}

.city-item span {
    color: $text-color-grey;
    font-size: $fz_14;
    float: left;
    margin-right: 8px;
}

.city-item p {
    text-align: left;
}

.city .more-news {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    // position: absolute;
    // bottom: 24px;
}

.more-news button {
    width: 32px;
    height: 32px;
    border: 1px solid #979EA9;
    border-radius: 50%;
    background: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 16px;
    cursor: pointer;
}

.city .more-news button i {
    color: #182840;
}

@media (max-width: 1340px) {
    .block-1-article {
        grid-template-columns: repeat(2, 1fr);
    }

    // .block-1-content-item {
    //     max-width: 327px;
    // }

    .block-1-regions-content {
        overflow-x: auto;
    }
}

@media (max-width: 992px) {
    #block-1 {
        padding: 36px 44px;
    }

    .block-1-article {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 768px) {
    #block-1 {
        padding: 0;        
    }

    .block-1-wrapper {
        grid-template-columns: minmax(304px, 1fr);
    }

    .block-1-news-feed {
        padding: 40px 24px;
        border-radius: 0;
        .download-more {
            margin-top: 8px;
        }
    }

    .block-1-news-feed-item {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-direction: column;
        // gap: 10px;
    }

    .block-1-news-feed-item .lifestyle-img {
        display: block;
    }

    .block-1-content-wrapper {
        padding: 0 24px 40px 24px;
    }

    .block-1-content-wrapper .download-more {
        display: flex;
        margin-top: 32px;
    };

    .block-1-content-item {
        max-width: 100%;
    }
}

@media (max-width: 720px) {
    .block-1-article {
        grid-template-columns: 1fr;
        row-gap: 32px;
    }
}

@media (max-width: 680px) {
    .block-1-content-wrapper .btn-group {
        display: flex;
    }
    .block-1-wrapper {
        grid-template-columns: minmax(328px, 1fr);
    }
}