@import '../../SCSS/variables.scss';

#footer {
    background: $bg-color-grey;
}

.footer-wrapper {
    border-bottom: 1px solid #DEDFE0;
    padding-bottom: 62px;
}

.footer-container {
    padding: 56px 0 62px 0;
    border-top: 1px solid $border-color;
    border-bottom: 1px solid $border-color;
    padding: 32px 44px 64px 44px;
}

.site-map {
    // display: grid;
    // grid-template-columns: repeat(6, auto);
    // gap: 125px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    // gap: 16px 120px;
    gap: 24px;
}

.column {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 24px;
    .uppercase {
        text-transform: uppercase;
    }
    .column-item {    
        text-align: left;
        width: 100%;
        .column-item-title {
            display: flex;
            justify-content: space-between;
            color: $text-color-black;
            font-size: $fz_16;
            font-weight: 600;
            line-height: 130%;
            margin-bottom: 8px;
            i {
                display: none;
            }
        }
        .contains-content {
            margin-bottom: 8px;
        }
        .column-dropdown {
            display: flex;
            flex-direction: column;
            gap: 8px;            
            a {
                color: $text-color-footer;
                font-size: $fz_14;
                font-weight: 400;
                line-height: 140%;
                transition: all 0.1s ease-in-out;
                .uppercase {
                    text-transform: uppercase;
                }
            }   
        }
    }
            
}

// .turnArrow {
//     margin-bottom: 24px;
// }

.arrow-up-down {
    transform: rotate(180deg);
}

.active {
    display: none;
}

.column-last {    
    row-gap: 24px;
    .column-item-title {
        display: flex;
        justify-content: space-between;
        color: $text-color-black;
        font-size: $fz_16;
        font-weight: 600;
        line-height: 130%;
        .uppercase {
            text-transform: uppercase;
        }
    }
}

#footer a:hover {
    text-decoration: underline;
}

.convenient-news-format {
    color: $text-color-black;
    font-size: $fz_24;
    font-weight: 600;
    line-height: 140%;
    text-align: left;
    margin-top: 40px;
}

.footer-social {
    display: flex;
    justify-content: left;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 32px;
    .social-left {
        display: flex;
    }
    .social-right {
        display: flex;
    }
    a {
        display: flex;
        align-items: center;
        margin-right: 32px;
        font-size: $fz_14;
        color: $text-color-social;
        img {
            margin-right: 12px;
        }
    }
    span {
        margin-top: 3px;
    }
}

.footer-divider {
    height: 1px;
    width: 100%;
    color: #000;
}

.footer-copyright-policy {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.copyright {
    padding-top: 24px;
    margin-bottom: 24px;
    color: #515151;
    font-size: $fz_14;
    font-weight: 400;
    text-transform: uppercase;
}

.policy {
    max-width: 564px;
    display: flex;
    justify-content: space-between;
    gap: 32px;
    padding-bottom: 32px;
    font-weight: 400;
    a {
        color: #919191;
        font-size: $fz_14;
    }
}

@media (max-width: 992px) {
    .footer-container {
        padding: 32px 44px 64px 44px;
    }
}

@media (max-width: 870px) {
    .convenient-news-format {
        text-align: center;
        font-size: 22px;
    }

    .footer-social {
        justify-content: center;
        gap: 24px;
    }

    .social-left, .social-right {
        flex-direction: column;
        gap: 24px;
    }
}

@media (max-width: 768px) {
    .footer-container {
        padding: 24px 24px 40px 24px;
    }

    .footer-wrapper {
        padding-bottom: 40px;
    }

    .column-last {
        row-gap: 24px;
        flex-basis: 100%;
        align-items: center;
    }

    .copyright {
        padding-top: 32px;
    }
}

@media (max-width: 700px) {
    .policy {
        flex-direction: column;
    }
}

@media (max-width: 576px) {
    .site-map {
        gap: 0;
    }

    .column {
        width: 100%;
        gap: 0;
        .column-item {
            border-bottom: 1px solid #DEDFE0;
            padding: 16px;
            gap: 16px;
            .column-item-title {
                margin-bottom: 0;
            }
            .contains-content {
                margin-bottom: 24px;
            }
            .column-item-title i {
                display: block;
            }
            .column-dropdown {
                margin-left: 16px;
                display: none;
                gap: 16px;
            }
            .active {
                display: flex;
            }
        }
    }
    .column-last {
        row-gap: 24px;
        margin-top: 40px;
    }
}